.Home{text-align:center;display:flex;justify-content:center;align-items:center;font-family:"Open Sans", sans-serif}.Home-logo{-webkit-animation:Home-logo-spin infinite 20s linear;animation:Home-logo-spin infinite 20s linear;height:40vmin;pointer-events:none}.Home-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.Home-link{color:#61dafb}@-webkit-keyframes Home-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes Home-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.login{width:27em;height:40em;display:flex;justify-content:center;align-items:center;margin-top:60px;position:relative;z-index:99}.login .container{display:flex;justify-content:center;align-items:center;background-color:#fff;box-shadow:0px 0px 6px 1px rgba(36,41,46,0.85);border-radius:4px;position:relative;z-index:99;width:100%;height:100%;z-index:99;padding:17px 10px}.login .right-side{display:flex;flex-direction:column;justify-content:center;height:100%;background-color:#24292e;width:100%;position:absolute;right:-34%;border-radius:6px;z-index:1;transition:all 400ms ease-in-out;cursor:pointer;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.281)}.login .right-side.right{right:-40%;align-items:flex-end}.login .right-side.right:hover{right:-45%}.login .right-side.left{right:40%;align-items:flex-start}.login .right-side.left:hover{right:45%}.login .right-side .text{font-size:21px;font-weight:bold;color:#fff;margin-right:3em;margin-left:3em}.btn{font-size:21px;padding:5px 20px;border:0;background-color:#24292e;color:#fff;border-radius:3px;transition:all 250ms ease-in-out;cursor:pointer}.btn:hover{background-color:#525050}.btn:focus{outline:none}

